import {TOGGLE_SUBSCRIBE_POPUP} from "../types";


const initialState = {
    subscribePopupOpened: false,
}

export const UIReducer = (state = initialState,action) => {
    const {type,payload} = action

    switch (type) {
        case TOGGLE_SUBSCRIBE_POPUP:
            return {
                ...state,
                subscribePopupOpened: payload
            }
        default:
            return state
    }
}